.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 1rem;
  position: fixed;
  width: 100%;
  height: 60px;
  z-index: 10;
}
.header-bg {
  background-color: rgba(0, 0, 0, 0.85);
  transition: 0.4s;
}
.nav-menu {
  display: flex;
}

.nav-menu li {
  padding: 0 1rem;
  list-style: none;
}

.nav-menu li a {
  font-size: 1.2rem;
  font-weight: 500;
  list-style: none;
}

.web-link {
  color: #ffffff;
  text-decoration: none;
}

.ham-menu {
  display: none;
}
.header-bg {
  background-color: rgba(0, 0, 0, 0.85);
  transition: 0.5s;
}

@media screen and (max-width: 640px) {
  .nav-menu {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    height: 100vh;
    background: rgba(0, 0, 0, 0.9);
    position: absolute;
    top: 0;
    left: -100%;
    z-index: -3;
    transition: 0.55s;
  }

  .nav-menu.active {
    left: 0;
  }

  .nav-menu li {
    padding: 1rem 0;
  }
  .nav-menu li a {
    font-size: 1.2rem;
    font-weight: 500;
  }
  .ham-menu {
    display: initial;
  }
}
