.bg-mask {
  width: 100%;
  height: 100vh;
  position: relative;
}
.home-image {
  width: 100%;
  height: 100%;
  background-size: cover;
  background-position: center top;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #000000;
  opacity: 1;
}

.bg-mask::after {
  content: "";
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: #000000;
  opacity: 0.8;
}

.home-main {
  height: 100%;
  width: 100%;
}

.home-main .content {
  position: absolute;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  text-align: center;
}

.home-main .content p {
  font-size: 1.4rem;
  font-weight: 300;
  text-transform: uppercase;
}

.home-main .content h1 {
  font-size: 4rem;
  padding: 0.6rem 0 1.5rem;
}
.content .btn {
  margin: 1.2rem 0.5rem;
}
@media screen and (max-width: 640px) {
  .home-main .content p {
    font-size: 1.4rem;
  }

  .home-main .content h1 {
    font-size: 3rem;
  }
}
